import React, { useEffect, useRef, useState } from 'react'
import * as d3 from 'd3';
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import './BarChartC.scss';

gsap.registerPlugin(ScrollTrigger);

const BarChartCMobile = (props) => {
  const barChartCMobile = useRef(null)
  const summary = React.createRef();
  const [progress, setProgress] = useState(0);
  useEffect(() => {
    var data = [{
      "name": "19-06",
      "arrest": 73,
      "charge": 44,
      "desc": "",
      "date": "196"
    },
    {
      "name": "19-07",
      "arrest": 224,
      "charge": 88,
      "desc": "",
      "date": "197"
    },{
      "name": "19-08",
      "arrest": 751,
      "charge": 171,
      "desc": "",
      "date": "198"
    },{
      "name": "19-09",
      "arrest": 765,
      "charge": 202,
      "desc": "",
      "date": "199"
    },{
      "name": "19-10",
      "arrest": 1189,
      "charge": 296,
      "desc": "",
      "date": "1910"
    },{
      "name": "19-11",
      "arrest": 2899,
      "charge": 581,
      "desc": "",
      "date": "1911"
    },{
      "name": "19-12",
      "arrest": 687,
      "charge": 100,
      "desc": "",
      "date": "1912"
    },{
      "name": "20-01",
      "arrest": 669,
      "charge": 68,
      "desc": "",
      "date": "201"
    },{
      "name": "20-02",
      "arrest": 346,
      "charge": 24,
      "desc": "",
      "date": "202"
    },{
      "name": "20-03",
      "arrest": 377,
      "charge": 8,
      "desc": "",
      "date": "203"
    },{
      "name": "20-04",
      "arrest": 67,
      "charge": 6,
      "desc": "",
      "date": "204"
    },{
      "name": "20-05",
      "arrest": 929,
      "charge": 7,
      "desc": "",
      "date": "205"
    },{
      "name": "20-06",
      "arrest": 230,
      "charge": 5,
      "desc": "",
      "date": "206"
    }];
  
data = data.sort(function (a, b) {
    return d3.descending(a.name, b.name);
})

var margin = {
    top: 100,
    right: 0,
    bottom: 0,
    left: 0
};

var width = 960 - margin.left - margin.right,
    height = 1038 - margin.top - margin.bottom;

var svg = d3.select("#barChartCMobile").append("svg")
    .attr('viewBox', '0 0 ' + (width + margin.left + margin.right) + ' ' + (height + margin.top + margin.bottom))
    .append("g")
    .attr("transform", "translate(" + margin.left + "," + margin.top + ")");

var x = d3.scaleLinear()
    .range([0, width])
    .domain([0, d3.max(data, function (d) {
        return d.arrest;
    })]);
    

var y = d3.scaleBand()
    .rangeRound([height, 0])
    .domain(data.map(function (d) {
        return d.name;
    }));


var yAxis = d3.axisLeft()
    .scale(y)
    .tickSize(0)
    .tickFormat(function(d, i){return data[i].desc})



var bars = svg.selectAll("#barChartCMobile .bar")
    .data(data)
    .enter()
    .append("g")

bars.append("rect")
  .attr("class", "bar__arrest")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 150)
  .attr("width", function (d) {
      return x(d.arrest)/width * (width - 150);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2 + 6;
  })
  .attr("x", function (d) {
    return x(d.arrest)/width * (width - 240) + 160;
  })
  .text(function (d, i) {
      return (i === data.length - 1 ||  i === data.length - 6) ? d.arrest : "";
  })
  .style("fill", function (d, i) {
    return (i === data.length - 1) ? "#fff" : "#fff";
  })
  .style("font-size", "32px");

bars.append("rect")
  .attr("class", "bar__highlight")
  .attr("y", function (d) {
      return y(d.name);
  })
  .attr("height", y.bandwidth() * 0.85)
  .attr("x", 150)
  .attr("width", function (d) {
      return x(d.charge)/width * (width - 150);
  });

bars.append("text")
  .attr("class", "label")
  .attr("y", function (d) {
      return y(d.name) + y.bandwidth() / 2 + 2;
  })
  .attr("x", 0)
  .text(function (d) {
      return d.date;
  })
  .style("fill", '#fff')
  .style("font-size", "32px");
    
  var gy = svg.append("g")
    .attr("class", "y axis")
    .attr("transform", "translate( " + (width - 10)  + ", 0 )")
    .call(yAxis)
    
  // create a list of keys
  var keys = ["", ""]

  // Usually you have a color scale in your chart already
  var color = d3.scaleOrdinal()
    .domain(keys)
    .range(["#fff", "#C41F1F"]);

  // Add one dot in the legend for each name.
  var size = 30
  svg.selectAll("mydots")
    .data(keys)
    .enter()
    .append("rect")
      .attr("x", function(d,i){ return width - 275 + i*(size+85)})
      .attr("y", - margin.top + 18) // 100 is where the first dot appears. 25 is the distance between dots
      .attr("width", size)
      .attr("height", size)
      .style("fill", function(d){ return color(d)})

  // Add one dot in the legend for each name.
  svg.selectAll("mylabels")
    .data(keys)
    .enter()
    .append("text")
      .attr("x", function(d,i){ return width - 280 + size*1.4 + i*(size+85)})
      .attr("y", - margin.top + (size/2) + 20) // 100 is where the first dot appears. 25 is the distance between dots
      .style("fill", function(d){ return color(d)})
      .text(function(d){ return d})
      .attr("text-anchor", "left")
      .style("alignment-baseline", "middle")
      .style("font-size", "32px");
  
  ScrollTrigger.create({
    trigger: barChartCMobile.current,
    start: "top top",
    end: "+=300%",
    scrub: true,
    pin: "#barChartCMobile",
    invalidateOnRefresh: true
  });

  const barArray = gsap.utils.toArray("#barChartCMobile .bar__arrest");
  const barArrayHighlight = gsap.utils.toArray("#barChartCMobile .bar__highlight");
  const sentences = gsap.utils.toArray(".chart__title span");
  const numbers = gsap.utils.toArray(".number")

  ScrollTrigger.create({
    trigger: barChartCMobile.current,
    start: "top top",
    end: "+=200%",
    scrub: true,
    invalidateOnRefresh: true,
    snap: 1 / 3,
    onUpdate: self => {
      let index = Math.round(self.progress * (sentences.length - 1))
      setProgress(self.progress)
    },
  }); 

  barArray.forEach((bar, i) => {
    let tl2 = gsap.timeline({
      scrollTrigger: {
        trigger: barChartCMobile.current,
        scrub: true,
        start: "top top",
        end: "+=200%",
        invalidateOnRefresh: true
      }
    })
    .fromTo(bar,
      {
        scaleX: 1
      },
      {
        scaleX: 1,
        duration: 1,
      }
    )
    .fromTo(bar,
      {
        scaleX: 1
      },
      {
        scaleX: 2899 / data[i].arrest,
        duration: 1,
      }
    )
    tl2.set({}, {}, "+=1");
  });
  
  
  barArrayHighlight.forEach((bar_h, i) => {
    let tl = gsap.timeline({
      scrollTrigger: {
        trigger: barChartCMobile.current,
        scrub: true,
        start: "top top",
        end: "+=200%",
        invalidateOnRefresh: true
      },
    })
    tl.fromTo(bar_h,
      {
        opacity: 0
      },
      {
        opacity: 1,
        duration: 1,
      }
    )
    .fromTo(bar_h,
      {
        scaleX: 1
      },
      {
        scaleX: 2899 / data[i].arrest,
        duration: 1
      },
    )
    
    tl.set({}, {}, "+=1");
  });

  // numbers.forEach((number, i) => {
  //   let tl_label = gsap.timeline({
  //     scrollTrigger: {
  //       trigger: barchartC.current,
  //       scrub: true,
  //       start: "top top",
  //       end: "+=200%",
  //       invalidateOnRefresh: true
  //     },
  //   })
  //   tl_label.fromTo(number,
  //     {
  //       opacity: 1
  //     },
  //     {
  //       opacity: 0,
  //       duration: 1,
  //     }
  //   )
  // });
  
  }, [])

  return (
    <div
      className="chart section"
      ref={barChartCMobile}
      id="barChartCWrapperMobile"
    >
      <div id="barChartCMobile">
        <h2 className="chart__title">
          <span style={{ opacity: 2 - progress * 6}}>611</span>
          <span style={{ opacity: progress < 0.33 ? progress * 6 - 1 : 3 - progress * 6 }}></span>
          <span style={{ opacity: progress < 0.66 ? progress * 6 - 3 : 5 - progress * 6 }}>66220202</span>
          <span style={{ opacity: progress*6 - 5}}>17%1620</span>
        </h2>
        <svg viewBox="0 0 960 1038" className="chart__summary" ref={summary} style={{ opacity: progress*6 - 5}}>
          <g transform="translate(0,100)">
            <rect y="0" height="938" x="150" width="137.7" fill="white"></rect>
            <rect y="0" height="938" x="287.7" width="534.4" fill="#C41F1F"></rect>
            <rect y="2" height="934" x="822.1" width="137.7" fill="#222" stroke="#C41F1F" stroke-width="4"></rect>
            <text x="824.6" y="100" fill="#C41F1F" style={{fontSize: "60px", fontWeight: 700}}>17%</text>
            <text x="844.6" y="200" fill="#C41F1F" style={{fontSize: "30px"}}>
              <tspan x="844" y="200" dy="1.1em"></tspan>
              <tspan x="844" y="250" dy="1.1em"></tspan>
            </text>
          </g>
          <g transform="translate(0,50)">
            <rect y="0" height="1038" x="0" width="150" fill="#222"></rect>
          </g>
        </svg>
      </div>
      <div className="remark">{props.remark}</div>
      
      {/* <h2 className="chart__title">
        
      </h2>
      <h2 className="chart__title">
        17%1498
      </h2> */}
    </div>
  )
}

export default BarChartCMobile