import React, { useEffect } from 'react';
import './CircleChart.scss'
import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/ScrollTrigger";
import * as d3 from 'd3';

gsap.registerPlugin(ScrollTrigger);

const sampleData = [
  { label: '', value: 634 },
  { label: ' ', value: 123 },
  { label: '', value: 84 },
  { label: '', value: 75 },
  { label: '', value: 43 },
  { label: '', value: 39 },
  { label: '', value: 34 },
  { label: ' ', value: 30 },
  { label: '', value: 24 },
  { label: '  ', value: 27 },
  { label: '', value: 18 },
  { label: '', value: 18 },
  { label: '', value: 16 },
  { label: '', value: 15 },
  { label: '', value: 15 },
  { label: '', value: 13 }
]

const CircleChartMobile = (props) => {  
  const cover = React.createRef();
  const bg = React.createRef();
  const circleChart = React.createRef();
  const height = 1440;
  const width = 1400;
  const radius = 30;
  function wrap(text, width) {
    text.each(function() {
      let text = d3.select(this),
        words = text.text().split(/\s+/).reverse(),
        word,
        line = [],
        lineNumber = 0,
        lineHeight = 1.4, // ems
        x = text.attr("x"),
        y = text.attr("y"),
        dy = 1.1,
        tspan = text.text(null).append("tspan").attr("x", x).attr("y", y).attr("dy", dy + "em");
      while (word = words.pop()) {
        line.push(word);
        tspan.text(line.join(" "));
        if (tspan.node().getComputedTextLength() > width) {
          line.pop();
          tspan.text(line.join(" "));
          line = [word];
          tspan = text.append("tspan").attr("x", x).attr("y", y).attr("dy", ++lineNumber * lineHeight + dy + "em").text(word);
        }
      }
    });
  }
  useEffect(() => {
    const svg_a = d3.select(circleChart.current)
    .append("svg")
    .attr("viewBox", "0 0 " + width +  " " + height)
    .append("g")


    const circles_a = d3.entries(sampleData).map((d, i) => ({
      x: i == 0 ? 725 : 310 * ((i - 1) % 5) + 100,
      y: i == 0 ? 280: 280 * Math.floor((i - 1)/5) - Math.sqrt(d.value.value) * 8 + 800,
      r: Math.sqrt(d.value.value) * 8,
      label: d.value.label
    }));

    svg_a.selectAll("circle")
      .data(circles_a)
      .join("circle")
      .attr("cx", d => d.x)
      .attr("cy", d => d.y)
      .attr("r", d => d.r)
      .attr("fill", '#C41F1F')
    
    svg_a.selectAll('.text-label')
      .data(circles_a)
      .enter().append('text')
      .attr('class', 'text-label')
      .attr('text-anchor', 'middle')
      .attr('x', d => d.x)
      .attr('y', d => (d.y + 30 + d.r))
      .style('fill', '#fff')
      .text(d => d.label)
      .call(wrap, 100)
      .style("font-size", '24.5px')
      
  });
  return (
    <div className="chart section" ref={circleChart} id="circleChartMobile">
      <h2 className="chart__title">{props.title}</h2>
      <div className="remark">{props.remark}</div>
    </div>
  )
}

export default CircleChartMobile;